<template>
	<view class="flex flex-direction bg-white">
		<!-- 是否登录 -->
		<is-login v-if="isLoginShow" currentPage="organ" :forceIndex="forceIndex"></is-login>
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">编辑组织</block>
		</cu-custom>
		
		<uni-forms :rules="rules" :value="current" ref="form" validate-trigger="bind" err-show-type="undertext">
			<view class="cu-form-group solid-bottom">
				<view class="title text-bold"> 是否分店</view>
				<switch @change="change_isBranch" v-model="organtion_branch" class="cyan"
					:class="organtion_branch?'checked':''" :checked="organtion_branch?true:false"></switch>
			</view>
			<!-- 所属组织 -->
			<uni-forms-item  v-if="organtion_branch" name="parentId">
				<view class="cu-form-group solid-bottom">
					<view class="title text-bold" style="width: 180rpx;">所属组织</view>
					<luyj-select-lay style="width: 100%;" :loading="mearchLoading" zindex="3" name="name" placeholder="请选择所属组织" :options="organ_list"
					       :value="current.parentId" slabel="label" svalue="MchId" @selectitem="changeParent">
					 </luyj-select-lay>
				</view>
			</uni-forms-item>
			<uni-forms-item name="mchNo" :border="true">
				<view class="cu-form-group solid-bottom">
					<view class="title text-bold">{{ organtion_branch ? '分店代码':'组织代码' }}</view>
					<input :placeholder="organtion_branch? '请输入分店代码':'请输入组织代码'" name="mchNo" 
						@input="changeMchNo($event);binddata('mchNo', $event.detail.value,'form')"></input>
				</view>
			</uni-forms-item>
			<uni-forms-item name="name">
				<view class="cu-form-group solid-bottom">
					<view class="title text-bold">{{ organtion_branch ? '分店名称':'组织名称' }}</view>
					<input :placeholder="organtion_branch? '请输入分店名称' :'请输入组织名称'" name="name" 
						@input="changeName($event);binddata('name', $event.detail.value,'form')"></input>
				</view>
			</uni-forms-item>
			<!-- 所属行业：总部 -->
			<uni-forms-item v-if="!organtion_branch" name="industryId">
				<view class="cu-form-group solid-bottom">
					<view class="title text-bold">所属行业</view>
					<uni-data-picker placeholder="请选择行业" style="width: 75%;" :value="current.industryId" :preload="true" :localdata="industry_list"
						popup-title="请选择行业" @nodeclick="changeIndustry" ></uni-data-picker>
				</view>
			</uni-forms-item>
			<uni-forms-item name="area">
				<view class="cu-form-group solid-bottom">
					<view class="title text-bold">所在地区</view>
					<uni-data-picker ref="area" placeholder="请选择地区" style="width: 75%;"  :value="area" :preload="true" :localdata="area_list"
						popup-title="请选择地区" @nodeclick="nodeArea" name="area"></uni-data-picker>
				</view>
			</uni-forms-item>
			<view class="cu-form-group">
				<view class="action title text-bold">
					详细地址
				</view>
			</view>
			<uni-forms-item name="address">
				<view class="cu-form-group solid-bottom">
					<textarea maxlength="-1" placeholder="请输入详细地址" name="address"  
						@input="changeAddress($event);binddata('address', $event.detail.value,'form')"></textarea>
				</view>
			</uni-forms-item>
		</uni-forms>
		<button class="margin padding-lg bg-cyan cu-btn round" @click="create('form')">创建</button>
	</view>
</template>

<script>
	/**
	 * @description 编辑组织
	 * @author 陆彦捷
	 * @date 2021.07.02 暂时复制创建组织的功能
	 * @date 2021.07.19 所属组织包含缓存属性
	 * @date 2021.07.23 重新引用global
	 */
	import organ from '@/common/storage/organstorage.js'; // 组织
	import changeobj from '@/common/function/changeobjectecordname.js'; // 格式化值
	var _self;
	export default {
		data() {
			return {
				preview: false, // 是否进入上一页
				forceIndex: true, // 强制不判断，不跳转
				MchId : organ.getIndustryId(),		// 获取当前组织id
				// 用于获取的数据
				mearchLoading: false,	// 组织加载中
				organ_list: [], // 组织信息
				industry_list: [], // 行业信息
				area_list: [], // 区域信息列表
				// 提交数据
				organtion_branch: true, // 是否分店
				current: {
					parentId: 0, // 所属组织
					mchNo: '', // 组织机构代码
					name: '', // 名称(组织/分店名称)
					industryId: 0, // 所属行业Id
					areacode : "",	// 所在地区code
					area1: "", // 所在地区1
					area2: "", // 所在地区2
					area3: "", // 所在地区3
					address: '' // 地址
				}, // 当前表单值
				area : "" ,			// 所在地区值
				rules: {
					mchNo: {
						rules: [{
							required: true,
							errorMessage: '请输入代码'
						}]
					},
					name: {
						rules: [{
							required: true,
							errorMessage: '请输入名称'
						}]
					},
					address: {
						rules: [{
							required: true,
							errorMessage: '请输入详细地址'
						}]
					}
				}, // 表单规则(行业和所在地区不验证)(组织暂时不验证)

			}
		},
		onReady: function() {
			this.$refs.form.setRules(this.rules);
		},
		onLoad: function(options) {
			_self = this;
			this.getParams(options);

			// 获取当前用户Id及其请求消息
			this.Token = this.global.login.getToken(); // 获取Token
			this.UserId = this.global.login.getUserId(); // 获取UserId
			// 请求基本信息
			this.organ_request(); // 请求组织信息
			this.industry_request(); // 请求行业信息
			this.area_request();	// 请求地区信息
		},
		methods: {
			// ========================================== 初始化 ===================================================
			/** 获取传参
			 * @param {Object} options
			 */
			getParams(options) {
				this.preview = options.preview != 'false' ? Boolean(options.preview): false; // 是否绑定上一页
				// 可强制不跳转
				if (options.forceIndex != undefined) {
					_self.forceIndex = options.forceIndex;
				}
			},
			// ========================================== 监听事件 ===================================================
			/** 判断是否 分店
			 * @param {Object} e
			 */
			change_isBranch : function(e) {
				this.organtion_branch = e.detail.value;
				if(e.detail.value){
					// 是分店（清除行业值）
					this.current.industryId = 0;
				}else{
					// 不是分店（清除所属组织）
					this.current.parentId = 0;
				}
			},
			/** 修改所属组织
			 * @param {Object} index 索引
			 * @param {Object} item 当前项目
			 */
			changeParent: function(index, item) {
				this.current.parentId = item ? item.MchId : 0;
			},
			/** 修改组织代码
			 * @param {Object} e
			 */
			changeMchNo : function(e){
				this.current.mchNo = e.detail.value;
			},
			/** 修改名称
			 * @param {Object} e
			 */
			changeName : function(e){
				this.current.name = e.detail.value;
			},
			/** 修改行业 （点击行业选项时触发）
			 * @param {Object} item
			 */
			changeIndustry : function(item) {
				this.current.industryId = item.value;
			},
			
			/** 点击所在地区时(触发修改当前地区)
			 * @param {Object} e
			 */
			nodeArea : function(item){
				this.area = item.value;
				var selectedArray = this.$refs['area'].$data.selected;
				var length = selectedArray.length;
				// 分别获取area1、area2、area3的值
				this.current.area1 = length > 0 ? selectedArray[0].value : null;
				this.current.area2 = length > 1 ? selectedArray[1].value : null;
				this.current.area3 = length > 2 ? selectedArray[2].value : null;
			},
			/** 修改地址
			 * @param {Object} e
			 */
			changeAddress : function(e){
				this.current.address = e.detail.value;
			},
			//==================================== 提交并验证 ==============================================
			/** 表单验证失败执行方法
			 * @param {Object} erros
			 */
			message_erros(erros) {
				// 验证手机号、验证码验证失败
				if (erros.length > 0) {
					uni.showToast({
						title: erros[0].errorMessage,
						icon: 'none'
					});
					return;
				}
			},
			/**
			 * 创建组织
			 * @param {String} form 表单名称
			 */
			create(form) {
				this.$refs[form]
					.submit()
					.then(res => {
						// 表单验证成功，创建组织
						_self.organ_create();
					})
					.catch(errors => {
						_self.message_erros(errors);
					});
			},
			// ========================================== 请求方法 ====================================================
			/**
			 * 请求所有组织机构
			 */
			organ_request() {
				this.mearchLoading = true;
				uni.request({
					url: _self.website + '/api/SvcMerchant/Get',
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						_self.mearchLoading = false;
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								var data = result.data.Data;
								_self.organ_list = data;
								_self.organ_list.forEach(item=>{
									item.label = item.Name + '(' + item. MchNo +')';
								});
							}  else {
								// 其他错误
								console.log("其他错误");
							}
						} else {
							console.log("数据连接失败");
						}
					},
					fail: function(erros) {
						_self.mearchLoading = false;
						console.log(erros.errMsg);
					},
					complete: function() {}
				});
			},
			/**
			 * 请求所有行业
			 */
			industry_request() {
				uni.request({
					url: _self.website + '/api/Share/GetIndustry',
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								var data = result.data.Data;
								_self.industry_list = changeobj.change(data,"IndustryId","Name","Children");
								// 为行业添加"全部范围"
								var tempIndustry = {
									"value": 0,
									"text": "全部范围",
								};
								_self.industry_list.unshift(tempIndustry);
							}  else {
								// 返回结果错误
								console.log("返回错误结果");
							}
						} else {
							// 连接失败
							console.log("连接失败");
						}
					},
					fail: function(erros) {
						console.log(erros);
					},
					complete: function() {}
				});

			},
			/**
			 * 请求所有地区
			 */
			area_request() {
					uni.request({
						url: _self.website + '/api/Share/Areas',
						header: {
							Authorization: 'Bearer ' + _self.Token
						},
						method: 'GET',
						success: function(result) {
							if (result.statusCode == 200) {
								if (result.data.ResultCode == "0") {
									var data = result.data.Data;
									_self.area_list = changeobj.change(data,"code","name","Children");
									// 为地区添加"全部范围"
									var tempArea = {
										"value": "",
										"text": "全部范围",
									};
									_self.area_list.unshift(tempArea);
								}  else {
									// 返回结果错误
									console.log("返回错误结果");
								}
							} else {
								// 连接失败
								console.log("连接失败");
							}
						},
						fail: function(erros) {
							console.log(erros);
						},
						complete: function() {}
					});
			},
			/**
			 * 创建组织 / 分店
			 */
			organ_create() {
					uni.showLoading();
					uni.request({
						url: _self.website + '/api/SvcMerchant/Create',
						header: {
							Authorization: 'Bearer ' + _self.Token
						},
						data: _self.current,
						method: 'POST',
						success: function(result) {
							uni.hideLoading();
							if (result.statusCode == 200) {
								if (result.data.ResultCode == "0") {
									var data = result.data.Data;
									// 保存当前mchId
									console.log("返回的组织信息=> ",data , _self.current.parentId );
									if(_self.current.parentId != 0 && _self.MchId == 0){
										organ.setIndustryId(_self.current.parentId);		// 设置当前组织
									}else if(_self.MchId == 0) {
										organ.setIndustryId(data);		// 设置当前组织
									}
									// 创建成功
									uni.showModal({
										title:'创建成功',
										content:'创建组织/分店成功。点击确认进入引导页。',
										showCancel:false,
										success: (result) => {
											if(result.confirm){
												uni.navigateTo({
													url: '/pages/basic/page_guid?preview=true' 
												});
											}
										}
									});
								} else if (result.data.ResultCode == "401") {
									// 清除当前消息，并重新登录
									this.global.login.clearloginMessage();
									uni.showModal({
										title: '登录过期',
										content: '登录过期，请重新登录。',
										showCancel: false,
										success: function(result) {
											if (result.confirm) {
												uni.redirectTo({
													url: '/pages/login/login'
												});
											}
										}
									});
								} else {
									// 其他错误
									uni.showToast({
										title: result.data.ResultMsg,
										icon: 'none'
									});
								}
							} else {
								uni.showToast({
									title: '数据连接失败！',
									icon: 'none'
								});
							}
						},
						fail: function(erros) {
							uni.hideLoading();
							uni.showToast({
								title: erros.errMsg,
								icon: 'none'
							});
						},
						complete: function() {}
					});
				
			},
		}
	}
</script>

